import React from 'react';
import { NavLink } from 'react-router-dom';
import sBarStyles from './navbar.module.scss';

const Navbar = () => {
    return (
        <aside className={sBarStyles.sidebar}>
            <ul>
                <li><NavLink activeClassName={sBarStyles.active} to="/profile">Profile</NavLink></li>
                <li><NavLink activeClassName={sBarStyles.active} to="/feed">Feed</NavLink></li>
                <li><NavLink activeClassName={sBarStyles.active} to="/messages">Messages</NavLink></li>
                <li><NavLink activeClassName={sBarStyles.active} to="/users">Users</NavLink></li>
                <li><NavLink activeClassName={sBarStyles.active} to="/settings">Settings</NavLink></li>
            </ul>
        </aside>
    )
}
export default Navbar;